import noticeBarMemberDetail from "../../components/y-componnents/notice-bar/notice-bar-member-detail.vue"
import tuiCollapse from "@/components/tui-collapse/tui-collapse"

export default {
	components: {
		tuiCollapse,
		noticeBarMemberDetail
	},
	data() {
		return {
			tabbar: ["热门", "娱乐", "体育", "国内", "汽车"],
			dataList: [{
					label: "总金额",
					value: '123',
					intro: "总金额详情",
					current: 0,
					disabled: false
				},
				{
					label: "优惠券/抵用券",
					value: '123',
					intro: "优惠券/抵用券详情",
					current: 0,
					disabled: false
				},
				{
					label: "礼品卡/会员卡",
					value: '123',
					intro: "礼品卡/会员卡详情",
					current: 0,
					disabled: false
				}, {
					label: "积分抵现",
					value: '123',
					intro: "积分抵现详情",
					current: 0,
					disabled: false
				}, {
					label: "运费",
					value: '12',
					intro: "运费详情",
					current: 0,
					disabled: false
				},
			],
		};
	},
	methods: {
		change(e) {
			let index = e.index;
			let item = this.dataList[index];
			item.current = item.current == index ? -1 : index
		},
	}
}
